import * as types from '../action/types'

const initialState = {
    lastUpdate: 0,
    light: false
}

export default function clockReducer (state=initialState,action){
    // console.log('action :',action);
    
    switch (action.type) {
        case types.TICK_CLOCK:
          return {
              ...state,
              ...{lastUpdate: action.ts, light: action.light}
          }
        default:
            return state
    }
}